import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Card, Divider, Header, Icon } from "semantic-ui-react"
import Item from "../Item"

export default function ({ date, slug, tags }) {
  const {
    allMarkdownRemark: { nodes },
  } = useStaticQuery(
    graphql`
      query listPostsByTag {
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
          nodes {
            excerpt(truncate: true)
            timeToRead
            fields {
              slug
              tags
            }
            frontmatter {
              title
              date(formatString: "MMM D, YYYY")
            }
          }
        }
      }
    `
  )

  const unique = new Set(tags)
  const related_posts = nodes
    .filter((node) => {
      if (node.fields.slug === slug) return false
      const overlapped = node.fields.tags.filter((x) => unique.has(x))
      return overlapped.length > 3
    })
    .slice(0, 10)
  const posts = related_posts.length > 0 ? related_posts : nodes.slice(0, 5)

  return (
    <footer>
      <Divider horizontal>
        <Header as="h5">
          <Icon name="newspaper outline" />
          <Header.Content>
            {related_posts.length > 0 ? "관련 포스트" : "최신 포스트"}
          </Header.Content>
        </Header>
      </Divider>
      <br />
      <Card.Group>
        {posts.map((post) => (
          <Item key={post.fields.slug} item={post} />
        ))}
      </Card.Group>
    </footer>
  )
}
