import React from "react"
import { Segment, Container } from "semantic-ui-react"
import Header from "./Header"
import Content from "./Content"
import Footer from "./Footer"

export default function Detail({ node }) {
  const { html, timeToRead, frontmatter, fields } = node
  const { title, date } = frontmatter
  const { slug, tags } = fields
  return (
    <Segment basic as="article">
      <Container text>
        <Header title={title} date={date} timeToRead={timeToRead} tags={tags} />
        <br />
        <br />
        <Content html={html} />
        <br />
        <br />
        <br />
        <br />
        <Footer date={date} slug={slug} tags={tags} />
      </Container>
    </Segment>
  )
}
