import React from "react"
import { Icon, Label } from "semantic-ui-react"

import koreanTags from "../../data/koreanTags.json"
import Tag from "./Tag"

const koreanRegex = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/

export default function Tags({ tags }) {
  const expandedTags = new Set(
    tags
      .filter((tag) => !koreanRegex.test(tag))
      .reduce((acc, cur) => {
        const tagObjects = [{ name: cur, to: cur }]
        if (cur in koreanTags) {
          tagObjects.push({ name: koreanTags[cur], to: cur })
        }
        return [...acc, ...tagObjects]
      }, [])
  )

  return (
    <Label.Group size="small" style={{ textAlign: "right" }}>
      <Icon name="tag" color="grey" />
      {[...expandedTags].map((tag) => (
        <Tag key={tag.name} tag={tag} />
      ))}
    </Label.Group>
  )
}
